@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    /* --- Light Theme (Default) --- */
    --color-white: 255 255 255; /* RGB for #ffffff */
    --color-dark: 34 34 34; /* RGB for #222222 */
    /* VARIATIONS (Tints are Lighter, Shades are Darker) */
    --color-white-shade: 243 244 246; /* Light Gray: Similar to gray-100 */
    --color-dark-shade: 17 24 39; /* Darker Gray: Similar to gray-900 */

    --color-white-tint: 255 255 255; /* Same as base white */
    --color-dark-tint: 107 114 128; /* Medium Gray: Similar to gray-500 */
    /* Add any other dynamic colors here: --color-primary: ... */
  }

  /* --- DARK THEME OVERRIDE (System Preference) --- */
  /* @media (prefers-color-scheme: dark) {
    :root {
      /* Base Color Switch */
  /* --color-white: 34 34 34; /* Base White (Now Dark Gray) */
  /* --color-dark: 255 255 255; /* Base Dark (Now Pure White) */

  /* VARIATIONS FOR DARK THEME */
  /* --color-white-shade: 55 65 81; /* Lighter gray for dark mode (shade of the dark base) */
  /* --color-dark-shade: 243 244 246; /* Lighter white for dark mode (shade of the light base) */

  /* --color-white-tint: 34 34 34; /* Same as base white */
  /* --color-dark-tint: 107 114 128; /* Darker gray for dark mode (tint of the light base) */
  /* }
  }*/

  /* The .dark class overrides the media query, allowing the manual toggle to stick */
  .dark-theme {
    /* Mirror the media query overrides for manual toggle */
    --color-white: 34 34 34;
    --color-dark: 255 255 255;

    --color-white-shade: 55 65 81;
    --color-dark-shade: 243 244 246;

    --color-white-tint: 34 34 34;
    --color-dark-tint: 107 114 128;
  }
}

// root
:root {
  --app-color-primary: #5a5a5a;
  --app-color-primary-shade: #090909;
  --app-color-primary-tint: #acacac;
  --app-color-secondary: #6497bf;
  --app-color-secondar-shade: #000042;
  --app-color-secondar-tint: #acacac;
  --app-color-accent: #6497bf;
  --app-color-accent-shade: #324b5f;
  --app-color-accent-tint: #ffffff;
  --app-color-white: #ffffff;
  --app-color-black: #000000;
  --app-color-dark: #222222;
}

// TODO prfer tailwind all through
.dark-theme {
  --app-color-primary: #5a5a5a;
  --app-color-primary-shade: #090909;
  --app-color-primary-tint: #acacac;
  --app-color-secondary: #6497bf;
  --app-color-secondar-shade: #000042;
  --app-color-secondar-tint: #acacac;
  --app-color-accent: #6497bf;
  --app-color-accent-shade: #324b5f;
  --app-color-accent-tint: #ffffff;
  --app-color-white: #222222;
  --app-color-black: #000000;
  --app-color-dark: #ffffff;

  body {
    background-color: var(--app-color-white);
  }

  .form-control {
    &:hover {
      color: var(--app-color-dark);
    }
    &:focus {
      color: var(--app-color-dark);
    }
    &:active {
      color: var(--app-color-dark);
    }

    label {
      color: var(--app-color-dark) !important;
    }

    color: var(--app-color-dark);
    background: var(--app-color-light);
  }

  textarea {
    color: var(--app-color-dark);

    &::placeholder {
      color: var(--app-color-dark);
      opacity: 0.8;
    }
  }

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active {
    color: var(--app-color-dark) !important;
    -webkit-text-fill-color: var(--app-color-dark) !important;
    // -webkit-box-shadow: 0 0 0 1000px white inset !important;
    -webkit-background-clip: text !important;
    background-clip: text !important;
    background: red !important;
    -webkit-box-shadow: 0 0 0 1000px var(--app-color-white) inset !important;
    -webkit-text-fill-color: var(--app-color-dark) !important;
    transition: background-color 5000s ease-in-out 0s;
  }

  input::placeholder {
    color: var(--app-color-dark);
  }

  .form-select {
    background: var(--app-color-light);
    color: var(--app-color-dark);
    &:hover {
      background: var(--app-color-light);
    }
    &:focus {
      background: var(--app-color-light);
    }
    &:active {
      background: var(--app-color-light);
    }
  }

  .form-label {
    color: var(--app-color-dark);
  }

  .company-img {
    filter: invert(1);
  }
}

.form-error {
  text-align: left !important;
}

.respect-visble-color-force {
  color: var(--app-color-dark) !important;
  background-color: var(--app-color-white) !important;
}

.toast-success {
  background-color: var(--app-color-secondary) !important;
  border-radius: 12px !important;
}

.toast-error {
  background-color: red !important;
  border-radius: 12px !important;
}

.poper-secondary {
  /* background: var(--app-color-secondary) !important;
  background-color: var(--app-color-secondary) !important;
  color: white;
  border: var(--app-color-secondary) !important;
  margin: var(--app-color-secondary) !important;*/
  background: var(--app-color-secondary);
  color: var(--app-color-white);
  border: 0;
  max-width: 250px;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 5px 8px 0 rgba(0, 0, 0, 0.14),
    0 1px 14px 0 rgba(0, 0, 0, 0.12);
  z-index: 999;

  .ngxp__arrow {
    background-color: var(--app-color-secondary) !important;
  }
}

.form-control {
  border-radius: 5px;
}

.scrool-bar-side {
  &::-webkit-scrollbar {
    width: 0; // Hide scrollbar by default
  }

  &:hover::-webkit-scrollbar {
    width: 6px; // Show scrollbar on hover
  }

  &::-webkit-scrollbar-track {
    // background: var(--app-color-white);
  }

  &::-webkit-scrollbar-thumb {
    background: var(--app-color-primary);
    border-radius: 6px;
  }
}

body {
  &::-webkit-scrollbar {
    width: 0; // Hide scrollbar by default
  }

  &:hover::-webkit-scrollbar {
    width: 6px; // Show scrollbar on hover
  }

  &::-webkit-scrollbar-track {
    // background: var(--app-color-white);
  }

  &::-webkit-scrollbar-thumb {
    background: var(--app-color-primary);
    border-radius: 6px;
  }
}

.prevent-print {
  @media print {
    z-index: 999 !important;
    display: none;
  }
}

.respect-print {
  @media print {
    z-index: 999 !important;
    margin-left: 0 !important;
    width: 100% !important;
  }
}
